<template>
  <div id="app">
<!--    <router-link to="/Homepage">home</router-link>-->
    <div id="background">
      

      <div id="clock">
        <!--      双向绑定-->
        <div id="time">{{time}}</div>
        <div id="date">{{date}}</div>
      </div>

    </div>
    <router-view></router-view>
  </div>
</template>

<script>
/*import HelloWorld from './components/HelloWorld.vue'*/

/*import HomePage from "../src/components/HomePage.vue";
import IndexPage from "../src/components/IndexPage.vue";*/

export default {
  name: 'App',
 /* components: {
    HomePage,
    IndexPage
  },*/
  data(){
    return{
      time:'',
      date:'',
    }
  },
  mounted() {

    this.$nextTick(()=>{
      setInterval(this.update_clock,1000);
    })
  },
  methods:{
    update_clock:function(){
      var d =new Date;
      var year =d.getFullYear();
      if (year<10){
        year="0"+year;
      }
      var mon=d.getMonth();
      if (mon<10){
        mon="0"+(mon+1);
      }
      var day=d.getDay();
      day=day+10;
      if (day<10){
        day="0"+day;
      }
      var hour=d.getHours();
      if (hour<10){
        hour="0"+hour;
      }
      var min=d.getMinutes();
      this.time=hour+":"+min;
      this.date=year+"/"+mon+"/"+day;
    }
  }
}
</script>

